@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');

*
{
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar
{
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar h1
{
  color: #f1356d;
}

.navbar a
{
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: #333;
}

.navbar a:hover
{
  color: #f1356d;
}

.content
{
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
}

.pContainer
{
  justify-content: space-between;
}

.pBtn
{
  border-color: #f1356d;
}

.pBtn:hover
{
  background-color: #f1356d;
  color: white;
}